<template>
	<view class="content">
		<u-navbar :is-back="true" title="更多详情" 	title-color='#fff' 
		back-icon-color='#fff' 
		:background="{ background:'#213244'}">
		</u-navbar>
		<view class="zhengti">
					<view class='title f9b'>
						<span>基础信息</span>
					</view>
						<view class='neirong flex-column start top'>
							<span>参考价格:{{activity.price}}</span>
							<span>开发商:{{activity.developers}}</span>
							<span>占地面积:{{activity.land}}</span>
							<span>建筑面积:{{activity.architecture}}</span>
							<span>楼盘地址:{{activity.address}}</span>
							<span>产权年限:{{activity.propertyRight}}</span>
							<span>楼栋总数:{{activity.building}}</span>
							<span>建筑类别:{{activity.category}}</span>
							<span>楼层情况:{{activity.floor}}</span>
							<span>总户数:{{activity.households}}</span>
							<span>停车位:{{activity.parking}}</span>
							<span>装修情况:{{activity.situation}}</span>
							<span>物业公司:{{activity.propertyCompany}}</span>
							<span>物业费:{{activity.cost}}</span>
						</view>
		</view>
	</view>
</template>

<script>
	import api from '../../api/index.js'
	import container from '../../components/container/index.vue'
	import LsSwiper from '../../components/ls-swiper/index.vue'
	import uniIcons from "@/components/uni-icons/uni-icons.vue"
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		components: {
			container,
			LsSwiper,
			uniIcons,
		},
		data() {
			return {
				base_lsit: [],//轮播图
				activity:[],//详情
			}
		},
		onLoad() {
			this.init();
		},
		methods: {
			init(){
				this.AkxhHdlists();
			},
			openinfo(url) {
			    uni.navigateTo({
			     url: url,
			    });
			},
			makePhoneCall(phone){
				uni.makePhoneCall({
				    phoneNumber: phone
				});
			},
			AkxhHdlists(){
				AkxhHd('/catalyzer/AkxhXq/list','GET').then(res => {
					if (res.data.code == 200) {
				console.log();
						let result = res.data.result;
						this.activity = result[0];
						this.activity.opening = result[0].opening.substring(0,10);
					}
				})
			},
			getBase() {
				api.base(100).then(res => {
					this.base_lsit = res
				})
			},
			clickItem(data) {
				console.log(data)
			},
			back() {
				uni.redirectTo({
					url: '/pages/index/index',
				})
			}
		}
	}
</script>

<style>
	.content .zhengti {
		padding: 10px;
	}
	.content .zhengti .title {
		
	}
	.content .zhengti .neirong span{
		margin-top: 10px;
	}
	
	
</style>
